import Page from 'components/Page';
import React from 'react';
import { format } from 'date-fns';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import states from "../api/states";
import PageSpinner from 'components/PageSpinner';
import { findAdvances, newAdvance, findUsers} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
import CustomToolbarSelectAdvances from "../components/CustomToolbarSelectAdvances";
class AdvancePage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            payfrom : '',
            payto : '',
            user : 'nn',
            user_type : 'nn',
            amount : '',
            xtoken:'',
            msg: '',
            error: '',
            downloadFile:true,
            preload:false,
            data: [],
            users:[],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findAdvances(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    data:response.data,
                });
            }).catch((respError) => {});
            
            findUsers(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    users:response.data,
                });
            }).catch((respError) => {});

          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    isValidEmailAddress = (email) => {
        if (/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
            return true;
        }
        return false;
    }

    rerenderParentCallback = () => {
      findAdvances(this.state.xtoken).then((response) => {
        this.setState({
            ...this.state,
            data:response.data,
        });
      }).catch((respError) => {
      });
    }
    format_date_str = (str) => {
        let new_str = str.split('-');
        let day = new_str[2].slice(0, 2);
        return format(new Date(new_str[0], new_str[1], day), 'MM/dd/yyyy');
    }
    firstUpper = (str) => {
      let string = str.toLowerCase();
      return string.charAt(0).toUpperCase() + string.slice(1);
    }
    handlePayFromInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, payfrom: v,});
    }
    handlePayToInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, payto: v,});
    }
    handleAmountInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, amount: v,});
    }
    handleUserTypeInput = (event) => {
        const v = event.target.value;
        findUsers(this.state.xtoken, v).then((response) => {
            this.setState({
                ...this.state,
                users:response.data,
                user_type: v,
            });
        }).catch((respError) => {});
    }
    handleUserInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, user: v,});
    }
    handleSaveChanges = () => {
        this.setState({ ...this.state, preload:true});
        newAdvance(this.state.xtoken, this.state)
        .then((response) => {
            if( response.status === 200)
            {
                this.setState({
                    ...this.state,
                    payfrom : '',
                    payto : '',
                    user : 'nn',
                    user_type : 'nn',
                    amount : '',
                    msg: response.message,
                    data: response.data,
                    preload:false,
                    error: '',
                });
                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    preload:false,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
                preload:false,
            });
            console.log('error', error);
        });
    }
    render() {
        const columns = [
            {
              name: 'Id',
              options: {
                display: false
              }
            },
            'User',
            {
                name: 'Amount',
                options: {
                  display:true,
                  customBodyRender: (value, tableMeta, updateValue) => (
                      <Label>${value}</Label>
                  )
                }
            },
            'Deduction from',
            'Deduction to',
            {
              name: 'Date posted',
              options: {
                display:true,
                customBodyRender: (value, tableMeta, updateValue) => (
                    <Label>{value}</Label>
                )
              }
            },
        ];

        const data2 = () => {
          let rtn = [];
          this.state.data.map(item => {
            rtn.push([
              item.id,
              item.user_label,
              item.amount,
              item.payfrom,
              item.payto,
              item.created_at,
            ]);
          });
          return rtn;
        }
      
        const options = {
            filter: true,
            selectableRows: 'single',
            selectableRowsOnClick:true,
            filterType: 'dropdown',
            responsive: 'vertical',
            draggableColumns: {
              enabled: true
            },
            rowsPerPage: 5,
            selectToolbarPlacement: 'replace',
            customToolbarSelect: (selectedRows, displayData, setSelectedRows) => (
              <CustomToolbarSelectAdvances rerenderParentCallback={this.rerenderParentCallback} selectedRows={selectedRows} displayData={displayData} setSelectedRows={setSelectedRows}  originalData={this.state.data} Xtoken={this.state.xtoken}/>
            ),
            downloadOptions: {
                filename: 'user-advance-excel-format.csv',
                separator: ';',
                filterOptions: {
                  useDisplayedColumnsOnly: true,
                  useDisplayedRowsOnly: true,
                }
            },
            onDownload: (buildHead, buildBody, columns, data2) => {
              if (this.state.downloadFile) {
                let val= `${buildHead(columns)}${buildBody(data2)}`.trim();
                return val;
              }
      
              return false;
            },
            onRowSelectionChange: (currentRowsSelected, allRows, rowsSelected) => {
              // console.log('currentRowsSelected', currentRowsSelected);
            },
            onRowsDelete: rowsDeleted => {
              // console.log(rowsDeleted, 'were deleted!');
            },
            onChangePage: numberRows => {
              // console.log(numberRows);
            },
            onSearchChange: searchText => {
              // console.log(searchText);
            },
            onColumnSortChange: (column, direction) => {
              // console.log(column, direction);
            },
            onViewColumnsChange: (column, action) => {
              // console.log(column, action);
            },
            onFilterChange: (column, filters) => {
              // console.log(column, filters);
            },
            onCellClick: (cellIndex, rowIndex) => {
              // console.log(cellIndex, rowIndex);
            },
            onRowClick: (rowData, rowState) => {
              // console.log(rowData, rowState);
            },
        };
      return (
        <Page title="Advance payment" breadcrumbs={[{ name: 'Payments', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>
                    Record advance payment
                    {this.state.preload && (<PageSpinner/>)}
                </CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">Repayment from(date)*</Label>
                      <Input onChange={this.handlePayFromInput}
                        type="date" 
                        value={this.state.payfrom}
                        name="bookdate"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Repayment to(date)*</Label>
                      <Input onChange={this.handlePayToInput}
                        type="date" 
                        value={this.state.payto}
                        name="bookdate"
                        placeholder=""
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">Advance amount*</Label>
                      <Input onChange={this.handleAmountInput}
                        type="number" 
                        value={this.state.amount}
                        name="number"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">User type*</Label>
                      <Input onChange={this.handleUserTypeInput} value={this.state.user_type} defaultValue={this.state.user_type} type="select" name="truck">
                        <option value="nn" disabled={true}>Select user group</option>
                        <option value="1">Drivers</option>
                        <option value="2">Dispatchers</option>
                      </Input>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
    
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleSelect">User*</Label>
                      <Input onChange={this.handleUserInput} value={this.state.user} defaultValue={this.state.user} type="select" name="user">
                        <option value="nn" disabled={true}>Select user</option>
                        {
                            this.state.users.map((stt, idx) => (
                                <option key={idx} value={stt.id}>{this.firstUpper(stt.fname) + " " + stt.lname}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Add advance</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
        </Row>

          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardBody>
                    <MUIDataTable title={'Loads'}
                    data={this.state.data.map(item => {
                      return [
                        item.id,
                        item.user_label,
                        item.amount,
                        item.payfrom,
                        item.payto,
                        item.created_at,
                      ]
                    })} columns={columns} options={options} />
                </CardBody>
              </Card>
            </Col>
          </Row>
    
        </Page>
      );
    }
}
export default AdvancePage;
