import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import IndeterminateCheckBoxIcon from "@material-ui/icons/IndeterminateCheckBox";
import { withStyles } from "@material-ui/core/styles";
import states from "../api/states";
import {editAdvance, deleteAdvance, findUsers } from "../api/api";
import {
  Button,
  ButtonGroup,
  FormGroup,
  Form,
  Label,
  Input,
  Card,
  CardBody,
  CardHeader,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Alert,
} from 'reactstrap';

const defaultToolbarSelectStyles = {
  iconButton: {
  },
  iconContainer: {
    marginRight: "24px",
  },
  inverseIcon: {
    transform: "rotate(90deg)",
  },
};

class CustomToolbarSelectAdvances extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
        showEditModal:false,
        saved:false,
        id: '',
        payfrom : '',
        payto : '',
        user : 'nn',
        user_type : 'nn',
        amount : '',
        xtoken:'',
        msg: '',
        error: '',
        users:[],
    };
  }
  hideEditModal = () => {
    this.setState({
      ...this.state,
      showEditModal:false,
    });
    if(this.state.saved)
    {
      this.props.rerenderParentCallback();
    }
  }
  handleEditPayFromInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, payfrom: v,});
  }
  handleEditPayToInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, payto: v,});
  }
  handleEditAmountInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, amount: v,});
  }
  handleEditUserTypeInput = (event) => {
      const v = event.target.value;
      findUsers(this.state.xtoken, v).then((response) => {
          this.setState({
              ...this.state,
              users:response.data,
              user_type: v,
          });
      }).catch((respError) => {});
  }
  handleEditUserInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, user: v,});
  }
  handleEditSaveChanges = () => {
      editAdvance(this.state.xtoken, this.state, this.state.id)
      .then((response) => {
        //   console.log('save error', response);
          if( response.status === 200)
          {
            this.setState({
              ...this.state,
              saved:true,
              error: '',
              msg: response.message,
            });
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
          });
          console.log('error', error);
      });
  }

  handleClickEditRow = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    findUsers(this.props.Xtoken, selectedRowData.user_type).then((response) => {
        this.setState({
            ...this.state,
            users:response.data,
        });
    }).catch((respError) => {});
    this.setState({
        showEditModal:true,
        id: selectedRowData.id,
        payfrom : selectedRowData.payfrom,
        payto : selectedRowData.payto,
        user : selectedRowData.user,
        user_type : selectedRowData.user_type,
        amount : selectedRowData.amount,
        xtoken:this.props.Xtoken,
    });
  }
  handleDeleteSelection = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    deleteAdvance(this.props.Xtoken, selectedRowData.id)
    .then((response) => {
      if(response.status === 200 )
      {
        alert(response.message);
        this.props.rerenderParentCallback();
        return;
      }
      alert(response.message);
      return;
    })
    .catch((error) => {
      alert('Connection error occured');
    });
  };

  handleClickDeselectAll = () => {
    this.props.setSelectedRows([]);
  };

  firstUpper = (str) => {
    let string = str.toLowerCase();
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  handleClickBlockSelected = () => {
    console.log(`block users with dataIndexes: ${this.props.selectedRows.data.map(row => row.dataIndex)}`);
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.iconContainer}>

        <Tooltip title={"Edit row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickEditRow}>
            <EditIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Deselect row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickDeselectAll}>
            <IndeterminateCheckBoxIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Delete row"}>
          <IconButton className={classes.iconButton} onClick={this.handleDeleteSelection}>
            <DeleteIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        {/* modals */}
        <Modal
          size="lg"
          isOpen={this.state.showEditModal}
          backdrop={false}>
          <ModalHeader>
            Edit advance info
            {this.state.error.length > 0 && (
              <div>
                <br></br><Alert color="danger">{this.state.error}</Alert>
              </div>
            )}
            {this.state.msg.length > 0 && (
              <div>
                <br></br><Alert color="success">{this.state.msg}</Alert>
              </div>
            )}
          </ModalHeader>
          <ModalBody>
            <Row>
                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="exampleEmail">Repayment from(date)*</Label>
                        <Input onChange={this.handleEditPayFromInput}
                            type="date" 
                            value={this.state.payfrom}
                            name="bookdate"
                            placeholder=""
                        />
                        </FormGroup>
                        <FormGroup>
                        <Label for="exampleEmail">Repayment to(date)*</Label>
                        <Input onChange={this.handleEditPayToInput}
                            type="date" 
                            value={this.state.payto}
                            name="bookdate"
                            placeholder=""
                        />
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>

                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="exampleEmail">Advance amount*</Label>
                        <Input onChange={this.handleEditAmountInput}
                            type="number" 
                            value={this.state.amount}
                            name="number"
                            placeholder=""
                        />
                        </FormGroup>
                        <FormGroup>
                        <Label for="exampleSelect">User type*</Label>
                        <Input onChange={this.handleEditUserTypeInput} value={this.state.user_type} defaultValue={this.state.user_type} type="select" name="truck">
                            <option value="nn" disabled={true}>Select truck</option>
                            <option value="1">Drivers</option>
                            <option value="2">Dispatchers</option>
                        </Input>
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>
        
                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="exampleSelect">User*</Label>
                        <Input onChange={this.handleEditUserInput} value={this.state.user} defaultValue={this.state.user} type="select" name="user">
                            <option value="nn" disabled={true}>Select user</option>
                            {
                                this.state.users.map((stt, idx) => (
                                    <option key={idx} value={stt.id}>{this.firstUpper(stt.fname) + " " + stt.lname}</option>
                                ))
                            }
                        </Input>
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.handleEditSaveChanges}>
              Save changes
            </Button>{' '}
            <Button color="secondary" onClick={this.hideEditModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default withStyles(defaultToolbarSelectStyles, { name: "CustomToolbarSelectAdvances" })(CustomToolbarSelectAdvances);